<script>
  import { createEventDispatcher } from 'svelte';
  const dispatch = createEventDispatcher();

	export let typeCollapse;
	export let type;
  export let types;

	function onToggle() {
		dispatch('toggle');
  }
  function onChange(value) {
		dispatch('change',{ value });
	}
</script>

