    height: 20px;
    outline: none;
    padding: 0px;
    position: absolute;
    right: 25px;
    top: 20px;
    width: 20px; }
    .header .btn-toggle:after, .header .btn-toggle:before {
      border: solid #565656;
      content: "";
      height: 10px;
      left: 0px;
      position: absolute;
      transition: all .2s;
      width: 20px; }
    .header .btn-toggle:after {
      border-width: 2px 0 0;
