      cursor: pointer; }

@media (max-width: 576px) {
  .header {
    background-color: #00d7de;
    border-color: #fff;
    border-radius: 0 0 20px 20px;
    color: #fff;
    width: 100%; }
    .header .title {
      line-height: 80px;
