      .header .btn-toggle:after, .header .btn-toggle:before {
        border-color: #fff; } }
</style>

<div class="header">
  <h5 class="title">{type.text} Tasks</h5>
  <button
    class="btn-toggle"
    class:show={typeCollapse}
    on:click={() => onToggle()}
  >
  </button>
  <div
    class="type-group"
    class:show={typeCollapse}
  >
    {#each types as {text, value}, index}
      <p
        class="type-item"
        on:click={() => onChange(value)}
      >
        {text} Tasks
      </p>
    {/each}
  </div>
</div>
